/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  background-color: #89a7d1;
  color: #ffffff;
  font-family: "Nunito Sans", sans-serif;
  font-size: 1.6rem;
}
@media (min-width: 768px) {
  body {
    height: 100%;
  }
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

.btn-azul {
  background-color: #023992;
  padding: 1.5rem 4rem;
  color: #ffffff;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 5rem 0;
}
@media (min-width: 768px) {
  .btn-azul {
    width: auto;
  }
}
.btn-azul:hover {
  background-color: #012560;
  cursor: pointer;
}

h1,
h2,
h3 {
  font-family: "Nunito Sans", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
  color: #ffffff;
}

p {
  color: #ffffff;
  font-size: 2rem;
}

.header {
  background-color: none;
  backdrop-filter: blur(1rem);
  z-index: 99;
  padding: 0 5rem;
}
.header.fijo {
  width: 100%;
  position: fixed;
}
@media (min-width: 768px) {
  .header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.header .logo {
  margin-top: 0.5rem;
}
.header .logo img {
  width: 5rem;
}
@media (min-width: 768px) {
  .header .logo img {
    margin: 0.5rem 0;
    width: 5rem;
  }
}

.navegacion a {
  color: #ffffff;
  font-size: 2rem;
  text-align: center;
  letter-spacing: 0.2rem;
}
.navegacion .uno {
  clip-path: circle(0% at 100% 0);
}
@media (min-width: 768px) {
  .navegacion .uno {
    clip-path: circle(150% at 100% 0);
  }
  .navegacion a {
    font-size: 1.6rem;
  }
}
.navegacion .dos {
  clip-path: circle(150% at 100% 0);
}
.navegacion .enlaces-container {
  position: fixed;
  z-index: -2;
  height: 100vh;
  width: 100%;
  background-color: #ffffff;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  cursor: pointer;
  transition: all 1s ease;
  padding: 1rem 0;
  margin-right: 2rem;
}
@media (min-width: 768px) {
  .navegacion .enlaces-container {
    margin-top: 0;
  }
}
.navegacion .enlaces-container .navegacion_enlace {
  padding: 2rem;
  border-bottom: 0.2rem solid #e1e1e1;
}
@media (min-width: 768px) {
  .navegacion .enlaces-container {
    background: none;
    position: initial;
    align-items: flex-start;
    width: auto;
    height: auto;
    display: grid;
    gap: 2.5rem;
    grid-template-columns: repeat(3, 1fr);
  }
  .navegacion .enlaces-container .navegacion_enlace {
    color: #ffffff;
    padding: 0;
    border: none;
  }
  .navegacion .enlaces-container a {
    border-top: 0.2rem solid transparent;
    border-bottom: 0.2rem solid transparent;
    transition: 0.2s ease-out;
  }
  .navegacion .enlaces-container a:hover {
    border-top: 0.2rem solid #6888c3;
    border-bottom: 0.2rem solid #6888c3;
    color: #e1e1e1;
  }
}

.contenedor-slider {
  width: 100%;
  max-height: 60rem;
  overflow-x: clip;
}
.contenedor-slider .slider-contenedor {
  width: 100%;
  display: flex;
}

.contenido-slider {
  background: #145d8a;
  padding: 0 3rem;
  width: 100%;
  height: 50rem;
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-shrink: 0;
}
.contenido-slider img {
  width: 8rem;
}
@media (min-width: 768px) {
  .contenido-slider img {
    width: 30rem;
  }
}
.contenido-slider section {
  width: 40%;
}
.contenido-slider section p {
  color: #ffffff;
  text-align: justify;
  font-size: 1.5rem;
}
@media (min-width: 768px) {
  .contenido-slider section p {
    font-size: 2.5rem;
    line-height: 3rem;
  }
}

.contenido-slider:nth-child(2n) {
  background: #023992;
}

.info-escuela {
  margin: 8rem 0;
}

.titulo-info-escuela {
  text-transform: uppercase;
  font-size: 3.5rem;
  font-weight: 900;
}

.contenedor-escuelas {
  display: grid;
  gap: 1fr;
  grid-template-columns: repeat(4, 1fr);
  gap: 2rem;
}
.contenedor-escuelas .contenedor-escuela {
  padding: 1rem;
  background-color: #ffffff;
  text-align: center;
}
.contenedor-escuelas .contenedor-escuela h3 {
  color: #1a1b15;
  font-size: 1.5rem;
  margin-bottom: 0.5rem;
}
.contenedor-escuelas .contenedor-escuela img {
  width: 15rem;
}

.seccion-evento {
  border-top: 2px solid #4b0e62;
  margin-bottom: 10rem;
}
.seccion-evento .contenedor-cita {
  display: flex;
  justify-content: space-between;
  gap: 2rem;
  margin: 8rem auto 4rem;
}
.seccion-evento .contenedor-cita-imagen {
  width: 40rem;
}

.contenedor-cita-texto {
  text-align: center;
}
.contenedor-cita-texto p {
  margin: 0.8rem;
  font-size: 2rem;
}
.contenedor-cita-texto span {
  font-weight: 900;
  font-size: 1.8rem;
  color: #1a1b15;
}
.contenedor-cita-texto .titulo-texto-cita {
  font-size: 2.5rem;
  font-weight: 900;
}

.footer {
  padding: 2rem;
  background-color: #7e9fcd;
  color: #ffffff;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}
@media (min-width: 768px) {
  .footer {
    justify-content: space-between;
  }
}

.contenedor-sociales {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  text-align: center;
}
@media (min-width: 768px) {
  .contenedor-sociales {
    flex-direction: row;
    gap: 4rem;
  }
}

.redes-sociales-logos {
  margin: 1px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 2rem 4rem;
}
@media (min-width: 768px) {
  .redes-sociales-logos {
    flex-direction: row-reverse;
    justify-content: space-between;
    padding: 0 4rem;
  }
}
.redes-sociales-logos p {
  color: #ffffff;
}

.fa {
  padding: 1rem;
  font-size: 2rem;
  width: 4rem;
  height: 4rem;
  text-align: center;
  text-decoration: none;
  border-radius: 50%;
}
.fa:hover {
  opacity: 0.7;
}

/* Set a specific color for each brand */
/* Facebook */
.fa-facebook {
  background: #3b5998;
  color: white;
}

/* Twitter */
.fa-twitter {
  background: #55acee;
  color: white;
}

.fa-instagram {
  background: linear-gradient(45deg, #405de6, #5851db, #833ab4, #c13584, #e1306c, #fd1d1d);
  color: #ffffff;
}